<template>
  <card>
    <template v-slot:title>
      Hitos Mensuales
    </template>
    <template v-slot:body>
      <div :class="$style.content">
        <div
          v-for="(d, i) in preOffset" 
          :key="`${i}o-d`"
          :class="[
            $style.day,
          ]">
        </div>
        <div
          v-for="(d, i) in dates" 
          :key="`${i}-d`"
          :class="[
            $style.day,
            feriados.some(f => d.date.isSame(f.fecha, 'day')) && $style.feriado,
            d.date.isBefore(today, 'date') && $style.past,
            d.date.isSame(today, 'date') && $style.today,
            [0, 6].includes(d.date.day()) && $style.weekend,
          ]">
          <div :class="$style.dayNumber">{{ d.date.format('D') }}</div>
        </div>
        <div
          v-for="(d, i) in postOffset" 
          :key="`${i}o-d`"
          :class="[
            $style.day,
          ]">
        </div>
      </div>
    </template>
  </card>
</template>

<script>
import moment from 'moment';
import Card from '@/components/Card';
import feriados from '@/utils/feriados.js';

export default {
  name: 'MonthlyCalendar',
  components: {
    Card,
  },
  props: {
    selectedDate: {
      type: Object,
      default: () => moment(),
    },
    ceremonies: {
      type: Array,
      default: () => [],
    },
    dates: {
      type: Array,
      default: () => [],
    },
  },
  computed: {
    today() {
      return moment();
    },
    preOffset() {
      return [...Array(this.dates[0].date.day())];
    },
    postOffset() {
      const cos = 6 - this.dates[this.dates.length - 1].date.day();
      return [...Array(cos)];
    },
  },
  data() {
    return {
      feriados: [],
    };
  },
  created() {
    moment.locale('es');
  },
};
</script>

<style module lang="scss">
.content {
  display: flex;
  padding: 0.5em 0;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  flex: 1;
  position: relative;
  &::after {
    position: absolute;
    content: 'Comming soon';
    font-size: 5em;
    transform: rotate(-25deg);
    top: 1.5em;
    opacity: 0.75;
    font-weight: bold;
    filter: unset;
    animation: jump 2s ease-in-out infinite;
  }
}
.day {
  display: flex;
  margin: 0;
  flex-direction: column;
  padding: 0;
  width: 14%;
  border: 1px dashed var(--dark-primary-color-20);
  background: var(--light-primary-color-30);
  transition: background-color 0.5s linear;
}
.dayNumber {
  display: flex;
  font-weight: bold;
  width: 1.15em;
  justify-content: center;
  font-size: 1.5em;
  background: var(--dark-primary-color-20);
  line-height: 1em;
  transition: background-color 0.5s linear;
}
@keyframes jump {
  33% {
    text-shadow: 0 5px #f37121, 0 10px #f2aaaa;
  }
  50% {
    transform: translate(0, 0) rotate(-20deg);
    text-shadow: 0 0px #8fc0a9, 0 0px #84a9ac;
  }
  66.67% {
    text-shadow: 0 -5px #d54062, 0 -10px #8fc0a9;
  }
}
</style>